<template lang="pug">
	transition(name="fade" mode="out-in")
		.shop(v-if="display" :ref="'shop' + shop.id" :class="{shop_open: current && !target}")
			.shop__wrap
				.shop__main
					router-link.shop__link(v-if="!single || target" @click.native.prevent="openShop(shop.id)" event="" to="/")
						span.shop__name {{ shop.name }}
						span.shop__trust
							span.shop__verify(v-if="shop.verified") ✓
							span.shop__rating {{ shop.rating }}
					h1.shop__link(v-else)
						span.shop__name {{ shop.name }}
						router-link(:to="'/' + shop.id + '/review'").shop__trust
							span.shop__verify(v-if="shop.verified") ✓
							span.shop__rating {{ shop.rating }}

					strong.shop__category {{ shop.category }}

					.shop__preview(@click="openShop(shop.id)" :class="{shop__preview_big: previewOpen && current}")
						img.shop__preview-img(:src="shop.preview" :class="{'shop__preview-img_zindex': previewOpen && current}")
					em.shop__address
						svg.shop__address-icon(class="svg-icon" viewBox="0 0 20 20")
