						| {{ shop.address }}, {{ shop.city }}, {{ shop.area }}
					.shop__meta(v-if="single")
						span.shop__worktime {{ shop.worktime }}
			iframe.shop__mapframe(v-if="current && !target" :src="shop.maplink" frameborder="0" style="border:0;" allowfullscreen="" aria-hidden="false" tabindex="0")
			.shop__description(v-if="single")
				h3.shop__description-title {{ $lang('cshop__description_title') }}
				p.shop__description-content {{ shop.description }}
			.shop__goods(v-if="current && !target")
				router-link.shop__goods-cart(to="/carts") {{ $lang('cshop__actions_cart') }} ({{ goodsAmount }})
				//- 	span(v-text="Object.keys(store.goods).length")
				button.shop__button-close(v-if="!single" @click="closeHandler") {{ $lang('cshop__actions_close') }}
				.shop__title(v-if="goods") {{ $lang('cshop__goods_title') }}
				CGood(v-for="good in goods" :store="shop" :good="good" :added="good.added")
</template>

<script>
import CGood from './CGood'
export default {
	name: 'CShop',
	props: {
		shop: Object,
		single: {
			type: Boolean,
			default: false
		},
		target: {
			type: Boolean,
			default: false
		}
	},
	data() {
		return {
			display: false,
			goods: null,
			previewOpen: false
		}
	},
	components: {
		CGood
	},
	mounted() {
		if(this.single) {
			this.openShop(this.shop.id);
		}

		if(this.shop.id) {
			if(this.current) {
				if(!this.target) {
					this.getGoods();
				}

				setTimeout(() => {
					window.scrollTo({
					  top: this.$refs['shop' + this.shop.id].offsetTop,
					  behavior: 'smooth',
					})
				}, 100)
			}

		}
		this.display = true
	},
	computed: {
		current() {
			return this.shop.id === this.$store.getters.shopId;
		},
		goodsAmount() {
			let amount = 0;
			const cart = this.$store.getters.cart;
			const shop = 's' + this.$store.getters.shopId;

			if(shop in cart) {
				for(const goodId in cart[shop].goods) {
					amount += cart[shop].goods[goodId].added;
				}
			}

			return amount;
		},
		worktime() {
			return shop.worktime
		}
	},
	methods: {
		previewClickHandler() {
			if(this.current) {
				this.previewOpen = !this.previewOpen;
			}
		},
		openShop(id) {
			// console.log('heeeer')
			if((this.current || this.target) && id) {
				this.$router.push('/' + id)
					.catch(() => {
						// console.log(err)
					});
				return;
			}

			// console.log(id)

			this.previewOpen = false;
			this.$store.commit('setShopId', id);
			// this.$store.commit('setCurrentStore', id);
			this.getGoods();

			setTimeout(() => {
				window.scrollTo({
				  top: this.$refs['shop' + id].offsetTop,
				  behavior: 'smooth',
				})
			}, 100)
		},
		getGoods() {
			// need do it on store and save on store
			// like this.$store.mutation(...)
			const shop = this.$store.getters.shopId;
			if(shop) {
				this.$req('goods?store=' + shop, 'GET')
					.then(res => {
						this.goods = res.goods
					})
			}
		},
		closeHandler() {
			const id = this.$store.getters.shopId;
			this.$store.commit('setShopId', null);

			setTimeout(() => {
				window.scrollTo({
				  top: this.$refs['shop' + id].offsetTop,
				  behavior: 'smooth',
				})
			}, 100)
		}
	}
}
</script>

